import http from '../httpConfig'
const table = 'mrbase_sys_flow_proxy_approval'
//业务申请代理

//业务申请代理列表
export const getBusinessApplyList = (params = {}) => {
  params['~table~'] = table
  return new Promise((resolve, reject) => {
    http.post('/api/operation_replace/list-replace/v1', params)
      .then(res => {
        resolve(res)
      })
      .catch(err => {
        reject(err)
      })
  })
}

