<template>
  <v-card>
    <v-card-text>
      <v-col cols="12">
        <mrc_ess_switch :true-value="1"
                        :false-value="0"
                        v-model="list.Is_used"
                        :true-text="$t('label_To_enable_the')"
                        :false-text="$t('label_disable')"></mrc_ess_switch>
        <mrc_ess_prompt :label="$t('label_The_acting_employee')+'*'"
                        :rules='false'
                        :errMsg="$errors('list','replaced_staff_uuid',requiredRule)"
                        v-model="list.replaced_staff_uuid"
                        obj="staffs">
        </mrc_ess_prompt>
      </v-col>
      <v-col cols="12"
             class="d-flex">
        <mrc_ess_datePicker @getTime='paramsChange'
                            :disabled='false'
                            :errMsg="$errors('list','effective_start',requiredRule.concat([{rule:'beforeDate',msg:'不能超过终止日期'}]))"
                            :label="$t('label_Start_date')+'*'"
                            v-model="list.effective_start"></mrc_ess_datePicker>
        <span style="height:58px;line-height:66px;font-size:21px;"
              class="pl-10 pr-10"> ></span>
        <mrc_ess_datePicker @getTime='paramsChange'
                            :label="this.$t('label_End_date')"
                            :disabled='false'
                            :errMsg="$errors('list','effective_end',requiredRule.concat([{rule:'afterDate',msg:'不能晚于开始时间'}]))"
                            v-model="list.effective_end"></mrc_ess_datePicker>
      </v-col>
      <v-col cols="12">
        <v-text-field :label="$t('label_Agency_reason')"
                      v-model="list.replace_reason"
                      :readonly="disabled">
        </v-text-field>
      </v-col>
      <v-col cols="12">
        <v-checkbox class="mx-2"
                    :true-value="1"
                    :false-value="0"
                    color="#ff8100"
                    :readonly="disabled"
                    v-model="list.Is_feedback"
                    :label="$t('label_Feedback')"></v-checkbox>
      </v-col>
      <v-col cols="12">
        <v-data-table :headers="showHeaders"
                      :items="blanklist"
                      sort-by="calories"
                      class="elevation-1"
                      hide-default-footer>
          <template #item.staffuuid="{item}">
            <span class="d-flex">
              <span v-for="(item1,index) in item.replace_staff_uuid"
                    :key='index'>
                {{item1}}
              </span>
            </span>
          </template>

          <template v-slot:item.actions="{ item }"
                    v-if="!disabled">
            <v-icon @click="editItem(item)">mdi-pencil</v-icon>
            <v-icon @click="deleteItem(item)"
                    class="m1">mdi-delete</v-icon>
          </template>

          <template v-slot:footer>
            <v-divider></v-divider>
            <span v-if="!disabled"
                  class="mt-2 pb-2 d-flex ml-5"
                  @click="editItem">
              <span :style="{color:$vuetify.theme.defaults.light.primary,lineHeight: '28px',fontSize:'12px'}">{{$t('label_New_business_function')}}</span>
              <v-icon class="material-icons"
                      :style="{color:$vuetify.theme.defaults.light.primary,fontSize:'24px'}">add</v-icon>
            </span>
          </template>
        </v-data-table>
      </v-col>

      <!-- 业务功能弹出框 -->
      <mrc_ess_myDialog v-model="dialog"
                        :title="$t('label_New_business_function')"
                        @success='save'
                        @cancel='close'>
        <v-card-text>
          <v-col cols="12">
            <!-- editedItem.replace_staff_uuid -->
            <mrc_ess_selectList :selectlist='list11'
                                :label="$t('label_agent')+'*'"
                                multiple
                                v-model="uuidlist"
                                @selectItem='selectItem'>
              <template #activator='{item}'>
                <span v-for="item1 in item"
                      :key="item1.uuid">
                  {{item1.uuid}}
                </span>
              </template>
              <template #listItem='{item}'>
                <p>{{item.name}}---{{item.uuid}}</p>
              </template>
            </mrc_ess_selectList>
          </v-col>
          <v-col cols="12">
            <mrc_ess_selectList :selectlist='list11'
                                :label="$t('label_Business_functions')+'*'"
                                v-model="editedItem.replace_function_uuid"
                                @selectItem='selectItem'>
              <template #listItem='{item}'>
                <p>{{item.name}}---{{item.uuid}}</p>
              </template>
            </mrc_ess_selectList>
          </v-col>
          <v-col cols="12">
            {{$t('label_Open_feature_list')}}
          </v-col>
          <v-col cols="12">
            <mrc_ess_selectList :selectlist='list11'
                                :label="$t('label_Business_button')+'*'"
                                v-model="editedItem.replace_button_uuid"
                                @selectItem='selectItem'>
              <template #listItem='{item}'>
                <p>{{item.name}}---{{item.uuid}}</p>
              </template>
            </mrc_ess_selectList>
          </v-col>
        </v-card-text>
      </mrc_ess_myDialog>
    </v-card-text>
    <mrc_ess_submitBtn @success='success'
                       @cancel='cancel'
                       :loading='btnIsLoading'
                       v-if='!disabled'></mrc_ess_submitBtn>
    <!-- <v-col cols="12">
      <mrc_ess_staffMsg v-model="list.apply_staff_uuid"
                        :iconShow='true'
                        :ErrMsg="$errors('list','apply_staff_uuid',requiredRule)"
                        :disabled='disabled'
                        :staffText="staffText"
                        @changeList="changeList"></mrc_ess_staffMsg>
    </v-col>
    <v-col cols="12">
      <mrc_ess_staffMsg v-model="list.handover_staff_uuid"
                        :disabled='disabled'
                        :ErrMsg="$errors('list','handover_staff_uuid',requiredRule)"
                        :iconShow='true'
                        :staffText="staffText2"
                        @changeList="changeList"></mrc_ess_staffMsg>
    </v-col> -->
    <!-- <div style="background-color: #f5f5f5"
         class="mt-4"
         v-if="list.apply_staff_uuid">
      <v-col cols="6">
        <v-text-field label="原公司"
                      v-model="company_Name"
                      :readonly="disabled">
        </v-text-field>
      </v-col>
      <v-col cols="6">
        <mrc_ess_prompt label="原部门*"
                        :rules='false'
                        :disabled='true'
                        v-model="viewList.dept_uuid"
                        :obj="deptuuid">
        </mrc_ess_prompt>
      </v-col>
      <v-col cols="12">
        <mrc_ess_prompt label="原岗位"
                        :rules='false'
                        :disabled='true'
                        v-model="viewList.job_post_uuid"
                        :obj="job_post_uuid">
        </mrc_ess_prompt>
      </v-col>
      <v-col cols="12">
        <mrc_ess_staffMsg v-model="viewList.direct_leader_code"
                          :disabled='true'
                          :staffText='staffText'></mrc_ess_staffMsg>
      </v-col>
      <v-col cols="12">
        <mrc_ess_prompt label="原角色*"
                        :rules='false'
                        :disabled='true'
                        v-model="viewList.role_uuids"
                        :obj="spica">
        </mrc_ess_prompt>
      </v-col>
    </div> -->
  </v-card>
</template>

<script> 
import {
  getBusinessApplyList
  , addBusinessApply
  , updateBusinessApply
  , deleteBusinessApply
  , detailBusinessApply
} from '@/service/api/businessApply.js'
import { required, maxLength, between, email, url } from 'vuelidate/lib/validators'
export default {
  components: {},
  props: {
    disabled: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      uuidlist: [
        {
          name: 'xaxa',
          uuid: 'xaxaxx'
        },
        {
          name: 'rtrt',
          uuid: '1255'
        },
      ],
      uuid: '1255',
      list11: [
        {
          name: 'xaxa',
          uuid: 'xaxaxx'
        },
        {
          name: 'rtrt',
          uuid: '1255'
        },
        {
          name: 'qwqwweq',
          uuid: '8855'
        }
      ],
      headers: [
        {
          text: this.$t('label_Business_functions'),
          align: 'start',
          sortable: false,
          value: 'replace_function_uuid',
        },
        {
          text: this.$t('label_button'),
          align: 'start',
          sortable: false,
          value: 'replace_button_uuid',
        },
        {
          text: this.$t('label_agent'),
          align: 'start',
          sortable: false,
          value: 'staffuuid',
        }
      ],
      dialog: false,
      list: {},
      btnIsLoading: false,
      viewList: {},

      blanklist: [],//新业务列表
      deleteblanklist: [],//删除列表
      editedIndex: -1,
      editedItem: {
        id: "",
        uuid: "",
        replace_staff_uuid: "",
        replaced_staff_uuid: "",
        replace_function_uuid: "",
        replace_button_uuid: "",
        role_uuid: "",
        replace_uuid: ""
      },
      defaultItem: {
        id: "",
        uuid: "",
        replace_staff_uuid: "",
        replaced_staff_uuid: "",
        replace_function_uuid: "",
        replace_button_uuid: "",
        role_uuid: "",
        replace_uuid: ""
      },
    };
  },
  computed: {
    company_uuid () { //当前选中公司uuid
      return this.$store.state.currentCompany
    },
    turnover_person () {
      return { prompt_name: 'turnover_person' }
    },
    showHeaders () {
      return this.headers
    },
  },
  created () { },
  watch: {
    disabled (v) {
      if (!v) {
        if (this.headers[this.headers.length - 1].value != 'actions') {
          this.headers.push(
            {
              align: 'center',
              sortable: false,
              value: 'actions',
            })
        }
      }
    }
  },
  async mounted () {
    if (!this.disabled) {
      this.headers.push(
        {
          align: 'center',
          sortable: false,
          value: 'actions',
        })
    }
    if (this.$store.state.uuid) {
      await this.getDetail()
      // this.$route.meta.title = this.$store.state.subPage_name
    }
    else {

    }
  },
  methods: {
    selectItem (item) {
      this.uuid = item.uuid
      this.uuidlist.push(item)
      console.log(item);
    },
    paramsChange () { },
    changeList (val) {
      this.viewList = val
    },
    async getDetail () {
      const res = await detailBusinessApply({ uuid: this.$store.state.uuid })
      if (res.code == 200) {
        this.list = res.data
        this.blanklist = res.mrbase_sys_apply_operation_replace_details.map(item => {
          item.replace_staff_uuid = String(item.replace_staff_uuid).indexOf(',') != -1 ? item.replace_staff_uuid.split(',') : [item.replace_staff_uuid]
          return item
        })
      }
      else {

      }
    },
    async success () {
      this.$v.list.$touch()
      if (this.$v.list.$error) return
      const mrbase_sys_apply_operation_replace_details = this.blanklist.map(item => {
        item.replace_staff_uuid = item.replace_staff_uuid.join()
        return item
      })
      if (this.$store.state.uuid) {
        updateBusinessApply({ ...this.$deletefield(null, this.list), mrbase_sys_apply_operation_replace_details }).then(res => {
          if (res.code == 200) {
            this.$Message({ text: this.$t('message_The_operation_was_successful') })
            this.getDetail()
          }
          else {
            this.$Message({ text: this.$t('message_The_modification_failed') + res.msg })
          }
        })
      }
      else {
        addBusinessApply({ ...this.list, mrbase_sys_apply_operation_replace_details }).then(res => {
          if (res.code == 200) {
            this.$Message({ text: this.$t('message_added_successfully') })
            this.$router.push({ name: 'staffConnect' })
          }
          else {
            this.$Message({ text: res.msg })
          }
        })
      }
    },
    cancel () {
      this.$myConfirm({
        content: this.$t('message_Confirm_the_cancellation_of_the_edit')
      }).then(res => {
        this.$router.push({
          name: 'staffConnect'
        })
      }).catch(err => { })
    },
    close () {
      this.dialog = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },

    save () {  //银行弹出框保存
      this.$v.editedItem.$touch()
      if (this.$v.editedItem.$error) return
      if (this.editedIndex > -1) {
        Object.assign(this.blanklist[this.editedIndex], this.editedItem)
      } else {
        this.blanklist.push(this.editedItem)
      }
      this.close()
    },
    editItem (item) {  //传了item就是修改  没传就是添加  默认传了事件对象。。
      // this.$v.editedItem.$reset()
      if (item.replace_staff_uuid) {
        this.editedIndex = this.blanklist.indexOf(item)
        this.editedItem = Object.assign({}, item)
      }
      this.dialog = true
    },
    deleteItem (item) {
      const index = this.blanklist.indexOf(item)
      this.$myConfirm({
        content: this.$t('message_Confirm_the_deletion')
      }).then(() => {
        const dd = this.blanklist.splice(index, 1)
        if (dd[0].uuid || dd[0].staff_uuid) this.deleteblanklist.push({ uuid: dd[0].uuid, "~type~": 'del' })
      })
    },
  },
  validations: {
    list: {
      replaced_staff_uuid: {
        required
      },
      effective_start: {
        required,
        beforeDate (value) {
          if (!this.list.effective_end) return true
          const prevData = new Date(value).valueOf()
          const nextData = new Date(this.list.effective_end).valueOf()
          return prevData < nextData
        }
      },
      effective_end: {
        required,
        afterDate (value) {
          if (!this.list.effective_start) return true
          const nextData = new Date(value).valueOf()
          const prevData = new Date(this.list.effective_start).valueOf()
          return prevData < nextData
        }
      },
    },
    editedtelItem: {
      replace_staff_uuid: {

      },
      replace_function_uuid: {

      },
      replace_button_uuid: {

      },
    },
  },
};
</script>

